import React, { PureComponent } from "react";
import ReactHtmlParser from "react-html-parser";
import Rating from "./PopUpRating";
import { isMobile, isTablet } from "react-device-detect";
import { STRING_TO_URL_CONVERT } from "../../../../utils";
import ContentWithOutSlack from "../../../Themes/Elements/PostContent/PostContentConvertion";

export default class PopUpContent extends PureComponent {
  scrollInterval = null;
  scrollControl = null;

  constructor(props) {
    super(props);
    this.state = {
      up: false,
      lastPosition: 0,
      timeForPopup: props.timeForPopup * 25,
      contentHeight: 0,
    };
    this.scrollContainerRef = React.createRef();
    this.scrollRef = React.createRef();

  }

  componentDidMount() {
    const { fullContent, isDisplay } = this.props;

    var authorHeight = document.querySelector(".tb_post_modal_post_author");
    var modal_bodyHeight = document.querySelector(".tb_post_modal_modal_body");
    var modal_post_wrapper = document.querySelector(".tb_post_modal_post_wrapper")
    var modal_content = document.querySelector(".tb_post_modal_content")

    if(modal_post_wrapper) modal_post_wrapper.onwheel = function(){ return isDisplay ? false : true }
    if(modal_post_wrapper) modal_content.scrollIntoView({ behavior: "smooth"});

    authorHeight = authorHeight ? authorHeight.clientHeight : 0;
    modal_bodyHeight = modal_bodyHeight
      ? modal_bodyHeight.clientHeight - authorHeight
      : authorHeight;

    this.setState({
      lastPosition: this.scrollContainerRef.current.scrollTop,
      contentHeight: modal_bodyHeight,
    });
    this.scrollControl =
      isDisplay &&
      fullContent &&
      fullContent.length > 100 &&
      setTimeout(() => this.startScrolling(), 2000);
  }

   disableMouseScroll=()=> {
    return false
}

  startScrolling = () => {

      this.scrollInterval = setInterval(() => {
        const { up, lastPosition } = this.state;
        const container = this.scrollContainerRef.current;

        if (container) {
          const position = container.scrollTop;
          const height = container.scrollHeight;
          if (position === lastPosition) this.setState({ up: !up });

          this.setState({ lastPosition: position });
          if (up) container.scrollTop = position - 1;
          else container.scrollTop = position + 1;

          if (container.scrollTop <= 0 || container.scrollTop + container.clientHeight >= height) this.setState({ up: !up });
        }
      }, 34);
  };

  componentWillUnmount() {
    if(this.scrollInterval) clearInterval(this.scrollInterval);
    if(this.scrollControl)  clearTimeout(this.scrollControl);
  }

  render() {
    const {
      contentTitle,
      item,
      Personalization,
      ThemeRule,
      mediaSize,
      fullContent,
      isDisplay,
    } = this.props;
    const fontSize = isMobile
      ? item.font.fontsize
      : isTablet
      ? item.font.fontsize + 2
      : item.font.fontsize + 5;
    const fontStyle = {
      fontFamily: ThemeRule.css_font,
      fontSize: fontSize,
      color: item.font.fontColor,
    };

    let contentData =
      item.network.id == 20
        ? isDisplay
          ? fullContent
          : this.props.contentData
        : STRING_TO_URL_CONVERT(
            isDisplay ? fullContent : this.props.contentData
          );
    if (item.network.id == 12) contentData = ReactHtmlParser(contentData); // for RRS but we have to change from backend.

    const contentDecorateClass = Personalization.textDecorate
      ? " tb_post_modal_text_decorate"
      : "";

    if (ThemeRule.themeId === 47) document.querySelector("html").style.overflow = "auto";

    return (
      <div
        className="tb_post_modal_post_wrapper"
        key={`content_${item.id}`}
        style={{
          height:
            item.type != 1 ? `${mediaSize.height - 90}px !important` : "auto",
        }}
      >
        <div className="tb_post_modal_post_wrap_in">
          <div
            className={`tb_post_modal_post_content tb-cTBfont-${ThemeRule.font_varient}`}
            style={fontStyle}
          >
            {item.rating ? (
              <Rating rating={item.rating} network={item.network} />
            ) : null}
            <div
              className={`tb_post_modal_content ${contentDecorateClass} auto-scroll-content`}
              id="scrollContainer"
              ref={this.scrollContainerRef}
              style={{
                height: isDisplay ? `${this.state.contentHeight}px` : ``,
              }}
            >
              {contentTitle ? (
                <div className="tb_post_bold_txt">
                  {ReactHtmlParser(contentTitle)}
                </div>
              ) : null}

              <ContentWithOutSlack
                data={item}
                content={contentData}
                Personalization={Personalization}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
