import React, { PureComponent } from "react";
import Network from '../../../Themes/Elements/Network/Network'
import { GET_MEDIA_NAME, isSocialWidget, timeAgo } from '../../../../utils'
import AuthorAvatar from "../../../Themes/Elements/Author/Elements/AuthorAvatar";


export default class PopUpAuthor extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      picture: props.item.author.picture,
      isAuthorImagevalid: true
    }
  }
  componentWillReceiveProps(nextProps) {
    const { item } = nextProps;
    this.setState({ picture: item.author.picture })
  }

  postRedirect = (link, isWidget) => {
    if(isWidget) if(link && link != "undefined") window.open(link, "_blank")
  }

  render() {
    const { item, ThemeRule, wall } = this.props;
  
    const authorImage = item.author.picture && String(item.author.picture).includes('author') ? " " : item.author.picture.replace(/[ ]+/g, "")
    const authorUserName = `${item.author.username && item.author.username.length > 0 ? `@` : ``}${item.author.username}`
    const authorDetailStatus = !(item.postAuthor == 0 || item.author.isInstaUser) ? true : false

    const dotStatus = !(item.postAuthor == 0 || item.timePost == 0 || item.author.isInstaUser) ? true : false
    const postTimeStatus = !(item.timePost == 0) ? true : false

    const authorNameStyle = {
      color: ThemeRule.authorColor
    };
    const sepratorStyle = {
      backgroundColor: ThemeRule.authorColor
    };

    return (
      <div className="tb_post_modal_post_author">
        <div className="tb_post_modal_author_details">
          
            <><div className="tb_post_modal_author_media" style={{ backgroundImage: `url(${authorImage})` }} onClick={event => this.postRedirect(item.link, !wall.isDisplay)}>
               {/* <AuthorAvatar network={item.network} username={item.author.name} authorClass={'tb_post_modal_author_media'} /> */}
            </div>
              <img loading="lazy" src={authorImage} alt={GET_MEDIA_NAME(authorImage)} height={"68"} width={"68"} style={{ display: `none` }}
                onError={(e) => {
                  this.setState({
                    isAuthorImagevalid: false
                  })
                }}
              />
            </>


          <div className="tb_post_modal_author_deatils">
             <div className="tb_post_modal_author_wrap">
              <div className="tb_post_modal_author_usrname" style={authorNameStyle} onClick={event => this.postRedirect(item.link, !wall.isDisplay)}>{item.author.name} </div>
            </div>
            <div className="tb_post_modal_author_info">
              <div className="tb_post_modal_author_handlename" style={authorNameStyle}>{authorUserName}</div>
               <div className="tb_post_modal_seprator_dot" style={sepratorStyle}> </div> 
              <span className="tb_post_modal_post_time" style={authorNameStyle}>{timeAgo(item.createdAt)}</span>
            </div>
          </div>
        </div>
        <div className="tb_post_modal_social_wrapper" onClick={event => this.postRedirect(item.link, !wall.isDisplay)}><Network network={item.network} networkClass={`tb_post_modal_social_ico tb__icon`} font={isSocialWidget() && ThemeRule.iconType == 505 ? ThemeRule.iconColor : item.font} ThemeRule={ThemeRule} /></div>
      </div >
    );
  }
}