import React, { PureComponent } from "react";
import ReactPlayer from "react-player"
export default class PopUpVideo extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            videoLoaded:false
        }
        this.VideoWrap = React.createRef();
    }

    setVideoLoaded = event => {
        this.setState({
            videoLoaded:event
        })
    }

    render() {
        const { data, wall } = this.props
        const { videoLoaded } = this.state
        return <div className={`tb_post_modal_video_holder${videoLoaded ? ' tb_post_video_loaded' : ''}`} ref={this.VideoWrap} key={data.mediaUrl} >
                    <ReactPlayer
                        className={'tb_post_modal_video'}
                        url={data.mediaUrl}
                        data-type="video"
                        data-network={data.networkId}
                        data-link={data.link}
                        data-wall-id={wall.Wall.id}
                        data-item-id={data.id}
                        data-load={0}
                        config={{
                            facebook: {
                                appId: '117029975354072',
                                attributes: {
                                    'data-width': 'auto',
                                    // 'data-height': `${document.querySelector('.ts_rt_post_in') ? `${document.querySelector('.ts_rt_post_in').clientHeight} px` : '100%'}`
                                }
                            },
                            // tracks: [
                            //     {kind: 'subtitles', src: `${CloudUrl}/media/images/subtitles.en.vtt`, srcLang: 'en', default: true},
                            // ]
                        }}
                        loop={true}
                        onError={(e) => { this.setVideoLoaded(false) }}
                        //(data.stories == 0) ? POPUP_VIDEO_RENEW(e) : null}
                        autoPlay={true}
                        muted={true}
                        volume={0}
                        playsinline={true}
                        onReady={(e) => {this.setVideoLoaded(true)}}
                        playing={true}
                        height={'100%'}
                        width={'100%'}
                        // controls={true}
                    />
                </div>
    }
}