import React, { PureComponent, Suspense } from "react";
import PopUpImage from "./PopUpImage";
import PopUpVideo from "./PopUpVideo";
import PopUpAudio from "./PopUpAudio";
const PopUpMediaSlider = React.lazy(() => import('./PopUpMediaSlider'));

export default class PopUpMedia extends PureComponent {
  mediaSize = mediaSize => {
    this.props.mediaSizeCallback(mediaSize)
  }

  render() {
    const { data ,wall } = this.props
    return <div className="tb_post_modal_media_holder">
      {
        data.imageList && data.imageList.length > 0 ?
          <Suspense fallback={<></>}><PopUpMediaSlider imageList={data.imageList} {...this.props} /></Suspense>
          : (data.type === 2 || data.type === 4) ?
            <PopUpImage wall={wall}  ImageUrl={data.postFileNew} {...this.props} mediaSizeCallback={this.mediaSize} opacity={1} />
            : (data.type === 3 || data.type === 5) ? data && data.isAudio ?
              <PopUpAudio {...this.props} />
              :
              <>
                <PopUpImage wall={wall}  ImageUrl={data.postFileNew} {...this.props} mediaSizeCallback={this.mediaSize} opacity={1} />
                <PopUpVideo  {...this.props} />
              </>
              : null
      }
    </div>
  }
}